          label: "身高不足"
        },
        {
          value: "视力障碍",
          label: "视力障碍"
        },
        {
          value: "上肢异常",
          label: "上肢异常"
        },
        {
          value: "下肢异常",
          label: "下肢异常"
        },
        {
          value: "监控照片信息不全",
          label: "监控照片信息不全"
        },
        {
          value: "准驾车型选择错误",
          label: "准驾车型选择错误"
        },
        {
          value: "其他",
          label: "其他"
        }
      ],
      Status:''
    };
  },
  methods: {
    //审核完成后请求
    getData(){
      this.loading = true
      axios({
        method: "post",
        url: http + "api/ExamineWeb/GetExamine",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        data: Qs.stringify({
          SessionKey: this.SessionKey
        })
      })
      .then(res => {
        this.loading = false
        console.log(res);
        if (res.data.IsSuccess) {
          this.examineData = res.data.Data
          if(!res.data.Data.BusinessNo){
            // this.examineData = this.examineDatas
            this.setTimer()
          }
        } else {
          this.$message({
            showClose: true,
            message: res.data.Message,
            type: "error"
          });
        }
      })
      .catch(error => {
        this.loading = false
        this.$message({
          showClose: true,
          message: "服务器错误",
          type: "error"
        });
      });
    },
    //提交审核结果
    auditData(){
      axios({
        method: "post",
        url: http + "api/ExamineWeb/Audit",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        data: Qs.stringify({
          BusinessNo: this.examineData.BusinessNo,
          Status:this.Status,
          SessionKey:this.SessionKey,
          AuditExplain:this.reason,
          TableBase64:this.imgURL
        })
      })
      .then(res => {
        this.loading = false
        console.log(res);
        if (res.data.IsSuccess) {
          this.$message({
            type: "success",
            message: res.data.Message
          });
          this.getData()
        } else {
          this.$message({
            showClose: true,
            message: res.data.Message,
            type: "error"
          });
        }
      })
      .catch(error => {
        this.loading = false
        this.$message({
          showClose: true,
          message: "服务器错误",
          type: "error"
        });
      });
    },
    //获取视频
    videoData(){
      this.loading = true
      axios({
        method: "post",
        url: http + "api/ExamineWeb/GetVideoUrl",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        data: Qs.stringify({
          BusinessNo: this.examineData.BusinessNo,
          DeviceID:this.examineData.DeviceID,
          SessionKey:this.SessionKey
        })
      })
      .then(res => {
        this.loading = false
        console.log(res);
        if (res.data.IsSuccess) {
          this.playerOptions.sources[0].src = 'http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4'
          this.$message({
            type: "success",
            message: res.data.Message
          });
        } else {
          this.playerOptions.sources[0].src = 'http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4'
          this.$message({
            showClose: true,
            message: res.data.Message,
            type: "error"
          });
        }
      })
      .catch(error => {
        this.loading = false
        this.$message({
          showClose: true,
          message: "服务器错误",
          type: "error"
        });
      });
    },
    openVisible() {
      this.dialogVisible = true;
    },
    openVisible2() {
      this.dialogVisible2 = true;
    },
    openVisible3() {
      this.dialogVisible3 = true;
      
    },
    //审核不通过
    openVisible4() {
      if (this.reason == "") {
        this.$message({
          type: "info",
          message: "请选择不合格原因"
        });
      } else {
        this.loading = true
        this.Status = -1
        this.auditData()
        this.dialogVisible4 = false;
        this.reason = "";
      }
    },
    openVisible6() {
      this.dialogVisible4 = false;
      this.reason = "";
    },
    //播放视频
    openVisible5(){
      this.videoData()
      setTimeout(()=>{
        this.dialogVisible5 = true
      },100)
      
    },
    //审核通过
    openVisible7() {
      this.$confirm("确定审核通过, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.loading = true
          this.Status = 1
          //DOM转图片
          let that = this
          let imgHeight = window.document.querySelector("#code").offsetHeight; // 获取DOM高度
          let imgWidth = window.document.querySelector("#code").offsetWidth; // 获取DOM宽度
          let scale = window.devicePixelRatio; // 获取设备像素比
          html2canvas(that.$refs.imageWrapper, {
            backgroundColor: null, //设置背景颜色
            useCORS: true, //允许图片跨域
            scale: scale, //缩放2倍，使得图片更加清晰
            width: imgWidth,
            height: imgHeight,
            imageTimeout: 5000, //设置图片的超时，设置0为禁用
            proxy: "", //url代理，用于加载跨域图源，为空则不会加载
            ignoreElements: element => {
              //用于忽略转换的图片中不需要的匹配元素，注意，为true才不会转换
              if (element.id == "mytitle") {
                return true;
              }
            }
          }).then(canvas => {
            let imgURL1 = canvas.toDataURL("image/png");
            console.log(imgURL1)
            that.imgURL = imgURL1.substr(22);
            that.auditData()
          })
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消操作"
          });
        });
    },
    // 定时请求数据
    setTimer() {
      this.timer = setInterval(() => {
        this.loading = true
        axios({
          method: "post",
          url: http + "api/ExamineWeb/GetExamine",
          headers: {
            "Content-Type": "application/x-www-form-urlencoded"
          },
          data: Qs.stringify({
            SessionKey: this.SessionKey
          })
        })
        .then(res => {
          this.loading = false
          console.log(res);
          if (res.data.IsSuccess) {
            this.examineData = res.data.Data
            if(res.data.Data.BusinessNo){
              this.clearTimer()
            }
          } else {
            this.$message({
              showClose: true,
              message: res.data.Message,
              type: "error"
            });
          }
        })
        .catch(error => {
          this.loading = false
          this.$message({
            showClose: true,
            message: "服务器错误",
            type: "error"
          });
        });
      }, 10000);
    },
    //清除定时器
    clearTimer() {
      clearInterval(this.timer)
    },
    //视频下载
    videoDowload() {
      this.videoData()
      setTimeout(()=>{
        window.open(this.playerOptions.sources[0].src);
      },100)
    }
  },
  mounted() {
    Vue.buttonAudio = document.getElementById("music");
    Vue.buttonAudio.pause();
    this.SessionKey = sessionStorage.getItem("SessionKey");
    this.ROLEID = sessionStorage.getItem("ROLEID")
    this.getData()
  },
  beforeDestroy() {
    this.clearTimer()
  },
  watch: {
    "examineData.BusinessNo": {
      handler: (val, olVal) => {
        if (!olVal&&val) {
          Vue.buttonAudio.play();
        }

      }
    },
    dialogVisible5: function(newVal) {
      if (newVal == false) {
        this.$refs.videoPlayer.player.pause();
      }
    }
  }
};
</script>
